* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  height: 100vh;
}

.heading {
   font-size:  50px;
   background-color: rgb(17, 144, 241);
   padding: 5px;
   color:azure;
}

.score {
  height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.choice {
  height: 30vh;
  display: flex;
  justify-content: center;
  margin: 0px 20px;
}

.computer-hand {
  width: 140px;
  height: 120px;
  margin: 50px 50px;
}

.user-hand {
  width: 140px;
  height: 120px;
  transform: rotateY(180deg);
  margin: 50px 50px;
}

.button-div {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.button {
  margin: 10px 10px;
  width: 150px;
  height: 50px;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(12, 126, 158);
  font-size: 20px;
  border-radius: 5px;
  cursor: pointer;
}
.button:hover{
  background-color: #218fad;
}

.button-restart{
  margin: 10px 10px;
  width: 150px;
  height: 50px;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(197, 27, 27);
  font-size: 20px;
  border-radius: 5px;
  cursor: pointer;
}
.button-restart:hover{
  background-color: tomato;
}